*{
    box-sizing: border-box;
    
  }
  
  
  .container{
    max-width:1170px;
    margin-left:auto;
    margin-right:auto;
    padding:1em;
   
  }
  
  ul{
    list-style: none;
    padding:0;
  }
  
  .brand{
    text-align: center;
  }
  
  .brand span{
    color:rgb(176, 229, 250);
  }
  
  .wrapper{
    box-shadow: 0 0 20px 0 rgba(72,94,116,0.7);
  }
  
  .wrapper > *{
    padding: 1em;
  }
  
  .company-info{
    background-image: url(../img/glowing-4634507_960_720.jpg);
    background-repeat: no-repeat;
    opacity: 0.6;
  }
  
  .company-info h3, .company-info ul{
    text-align: center;
    margin:0 0 5rem 0;
  }
  
  .contact{
    background:#e1e2e4;
  }
  
  /* FORM STYLES */
  .contact form{
    display: grid;
    grid-template-columns: 10fr 10fr;
    grid-gap:20px;
  }
  
  .contact form label{
    display:block;
  }
  
  .contact form p{
    margin:0;
  }
  
  .contact form .full{
    grid-column: 1 / 3;
  }
  
  .contact form button, .contact form input, .contact form textarea{
    width:100%;
    padding:1em;
    border:1px solid #f2b3ff;
  }
  
  .contact form button{
    background:#a9a6d6;
    border:0;
    text-transform: uppercase;
  }
  
  .contact form button:hover,.contact form button:focus{
    background:#92bde7;
    color:#fff;
    outline:0;
    transition: background-color 2s ease-out;
  }
  
  /* LARGE SCREENS */
  @media(min-width:700px){
    .wrapper{
      display: grid;
      grid-template-columns: 1fr 2fr;
    }
  
    .wrapper > *{
      padding:2em;
    }
  
    .company-info h3, .company-info ul, .brand{
      text-align: center;
    }
  }